.Paths {
padding: 0 80px;
}
.page-header {
  /* background-color: rgba(0,0,0,.1); */
  border: 3px dashed rgba(0,0,0,.3);
  border-top-right-radius: 20px;
  border-style: dashed;
  padding: 0 40px;
  margin-bottom: 40px;
}
.page-header>h3 {
  padding-left: 40px;
  padding-right: 40px;
}
.header-button {
border-top-right-radius: 10px;
margin-top: 0px;
padding: 5px 10px 5px 10px;
/* color: #fff;
background-color: rgba(0,0,0,.8); */
}
.paths-elements {
  padding: 0 10px 10px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.paths-elements>li {
  width: 100%;
}
.request-path-button {
  float: right;
  margin: 0;
}
.paths-elements {
  list-style-type: none;
}

@media only screen and (max-width: 600px) {
  .page-header>h3 {
    padding-left: 10px;
    padding-right: 10px;
  }
}



